:root {
  --body-font-size: 1.3rem;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Work Sans, sans-serif;
  line-height: 125%;
}

:not(.panel):not(.show-nav):not(.left):not(.right) {
  transition-property: margin, padding, width, font-size;
  transition-duration: 1s;
}

body {
  background-color: #f6f8fa;
}

h1 {
  color: #081646;
  margin-left: -5px;
  font-family: Sora, sans-serif;
  font-size: 3rem;
  font-weight: 600;
}

h2 {
  color: #081646;
  padding-bottom: 1rem;
  font-family: Sora, sans-serif;
  font-size: 2.5rem;
  font-weight: 600;
}

h3 {
  color: #081646;
  padding-bottom: 2rem;
  font-size: 1.75rem;
  font-weight: 600;
}

p {
  font-size: var(--body-font-size);
  color: #081646;
  padding-bottom: 1rem;
  line-height: 133%;
}

p:last-child {
  padding-bottom: 0;
}

p a {
  color: #1447c8;
  border-bottom: 2px solid #1447c8;
  text-decoration: none;
}

p a:hover, p a:focus, p a:active {
  font-weight: bold;
  text-decoration: none;
}

nav {
  font-size: var(--body-font-size);
  z-index: 1;
  background-color: #fff;
  border-bottom: 2px solid #44c6e640;
  justify-content: space-between;
  height: 100px;
  padding: 0 85px 0 8rem;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

nav .top, nav .left, nav .right {
  align-items: center;
  display: flex;
}

nav .left {
  flex-grow: 1;
}

nav #toggle {
  color: #1447c8;
  width: 2rem;
  margin-right: 2rem;
  font-size: 2rem;
  display: none;
}

nav a {
  color: #1447c8;
  margin-right: 2rem;
  text-decoration: none;
}

nav .link {
  border-bottom: 2px solid #fff;
  transition: all .2s;
}

nav .link:not(:focus):hover {
  border-color: #1447c8;
  font-weight: bold;
}

nav .link.active {
  color: #081646;
  border-color: #081646;
  font-weight: bold;
}

nav .link.active:hover {
  border-color: #081646;
}

nav #login {
  -webkit-user-select: none;
  user-select: none;
  text-transform: uppercase;
  text-align: center;
  color: #f6f8fa;
  font-weight: 600;
  font-size: var(--body-font-size);
  cursor: pointer;
  background-color: #1447c8;
  border-radius: 6px;
  padding: 1rem 1.5rem;
  text-decoration: none;
  transition: all .2s;
  display: inline-block;
}

nav #login:hover {
  background-color: #193787;
}

nav #login:active {
  color: #f6f8fa;
  background-color: #081646;
  transform: translateY(4px);
}

@media screen and (width <= 900px) {
  nav #login {
    font-size: 1em;
  }
}

nav #logo {
  border: none;
  width: 80px;
}

nav #logo img {
  width: 100%;
